import "@fontsource/roboto";
import { Container, Typography } from '@material-ui/core';
import Aanet from './components/Aanet.js';
import Nappi from './components/Nappi.js';
import Volume from './components/Volume.js';

function App() {
    let aani = new Audio("aanet/silence.mp3");
  const soita = (id) => {
    aani.currentTime = 0;
    aani.pause();
    if(id === "Kiberes Bibres"){
      aani = document.getElementById("audio");
      aani.play();
    }
    else if(id === "Koittakaa Jaksaa"){
      aani = document.getElementById("audio1");
      aani.play();
    }
    else if(id === "0/5"){
      aani = document.getElementById("audio2");
      aani.play();
    }
    else if(id === "Kuka Tarvitsee"){
      aani = document.getElementById("audio3");
      aani.play();
    }
    else if(id === "Sano Mummo"){
      aani = document.getElementById("audio4");
      aani.play();
    }
    else if(id === "Rutles"){
      aani = document.getElementById("audio5");
      aani.play();
    }
  }

  return (
      <Container>
        <Typography color="secondary" variant="h2">Niilo22 soundboard</Typography>
        <Nappi soita={soita}>Kiberes Bibres</Nappi>
        <Nappi className="nappi2" soita={soita}>Rutles</Nappi>
        <Nappi className="nappi3" soita={soita}>0/5</Nappi>
        <Nappi className="nappi4" soita={soita}>Koittakaa Jaksaa</Nappi>
        <Nappi className="nappi5" soita={soita}>Sano Mummo</Nappi>
        <Nappi className="nappi6" soita={soita}>Kuka Tarvitsee</Nappi>
        <Volume aani={aani}/>
        <Aanet/>
      </Container>
  );
}

export default App;
