import { Slider, Typography, Container, Icon, Grid } from '@material-ui/core'
import React, { useState } from 'react'
import { makeStyles } from '@material-ui/core/styles'
import { VolumeOff, VolumeUp} from '@material-ui/icons';

const useStyles = makeStyles({
    slider : {
        margin: 30,
        width: 740,
        color: "white"
    },
    text : {
        marginTop: 20,
        color: "white"
    },
    vola : {
        color: "white",
        fontSize: 85
    },
    box : {
        background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
        padding: 10,
        margin: 20,
        border: 2,
        borderRadius: 20,
    }
});


function Volume(props) {

    const styles = useStyles();

    const [voimakkuus, setVoimakkuus] = useState(30);

    const change = (event, newVolume) => {
        console.log(props.aani);
        setVoimakkuus(newVolume);
        let aani1 = document.getElementById("audio");
        let aani2 = document.getElementById("audio1");
        let aani3 = document.getElementById("audio2");
        let aani4 = document.getElementById("audio3");
        let aani5 = document.getElementById("audio4");
        let aani6 = document.getElementById("audio5");
        aani1.volume = voimakkuus / 100;
        aani2.volume = voimakkuus / 100;
        aani3.volume = voimakkuus / 100;
        aani4.volume = voimakkuus / 100;
        aani5.volume = voimakkuus / 100;
        aani6.volume = voimakkuus / 100;
    }

    return (
        <Container className={styles.box}>
            <Typography 
                className={styles.text}
                variant="h4"
                color="secondary"
            >Volume</Typography>
            <Grid container>
                <Grid item>
                    {
                    (voimakkuus === 0)
                    ? <Icon><VolumeOff className={styles.vola}/></Icon>
                    : <Icon><VolumeUp className={styles.vola}/></Icon>
                    }
                </Grid>
                <Grid item>
                    <Slider
                        onChange={change}
                        value={voimakkuus}
                        color="secondary"
                        className={styles.slider}
                    ></Slider>
                </Grid>
            </Grid>
        </Container>
    )
}

export default Volume;
